.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.inputs{
  display: flex;
  width: 400px;
}
 button, .inputs input{
  height: 30px;
  /* padding: ; */
}
button{
  color: #fff ;
  background-color: teal;
  outline: none;
  border: none;
  margin-top: 5px;
}
@media screen and (max-width: 768px){
  .app{
    width: 100%;
    overflow-x: hidden;
  }
  .inputs{
    width: 80%;
  }
}