li{
    display: flex;
    width: 400px;
    height: 75px;
    justify-content: flex-start;
    padding: 10px 8px;
    border: 1px solid white;
    margin: 10px 0;
}
input[type=checkbox]{
    height: 100%;
    width: 2rem;
    margin: 0 10px;
}
li button{
    justify-self: flex-end;
    font-size: 1rem;
    color: red;
    background-color: transparent;
    border: none;
    outline: none;
}
li button:hover{
    cursor: pointer;
}
ul{
    padding: 0;
}
.todoText p{
    font-size: 10px;
    padding: 0;
    margin: 0;
}
div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width:100%;
    text-align: left;
}
@media screen and (max-width: 768px){
    ul{
        width: 80%;
    }
    li{
        width: 100%;
        padding: 0;
    }
  }